import React from "react"
import { COLORS } from '../constants/colors';
import { TimelineItem }  from 'vertical-timeline-component-for-react';
import './CustomTimelineItem.style.css';

const CustomTimelineItem = ({ key, dateText, titleText, subTitleText, bodyText }) => {

    return (
        <TimelineItem
            key={key}
            dateText={dateText}
            dateInnerStyle={{ background: COLORS.blueHighlight, color: COLORS.trueWhite }}
            style={{ color: COLORS.blueHighlight }}
        >
            <div class='work-experience-timeline-item'>
                <h3 style={{ color: COLORS.blueHighlight }}>{titleText}</h3>
                <h4 style={{ color: COLORS.blueHighlight }}>{subTitleText}</h4>
                <p>
                    {bodyText}
                </p>
            </div>
        </TimelineItem>
    );
}

export default CustomTimelineItem;