import { FactConatiner } from '../components/FactContainer';
import { getWidthOfFactContainer, SCREEN_HEIGHT, SCREEN_WIDTH } from '../constants/sizes';
import { PageTitle } from '../components/PageTitle';
import './HomePage.style.css';
import { COLORS } from '../constants/colors';
import React from "react";
import { Timeline }  from 'vertical-timeline-component-for-react';
import CustomTimelineItem from '../components/CustomTimelineItem';
import ScrollArrow from '../components/ScrollArrow';
import { scroller } from "react-scroll";
import { Textfit } from 'react-textfit';

const HomePage = () => {
    const scrollToMainInfo = () => {
        scroller.scrollTo("homepage-main-info-container", {
            duration: 800,
            delay: 0,
            smooth: "easeInOutQuart",
        });
    }

    const scrollToTimeline = () => {
        scroller.scrollTo("homepage-work-experience-timeline-container", {
            duration: 800,
            delay: 0,
            smooth: "easeInOutQuart",
        });
    }

    const scrollToProjects = () => {
        scroller.scrollTo("homepage-projects-container", {
            duration: 800,
            delay: 0,
            smooth: "easeInOutQuart",
        });
    }

    const scrollToAboutMe = () => {
        scroller.scrollTo("homepage-about-me-container", {
            duration: 800,
            delay: 0,
            smooth: "easeInOutQuart",
        });
    }

    const scrollToContact = () => {
        scroller.scrollTo("homepage-contact-container", {
            duration: 800,
            delay: 0,
            smooth: "easeInOutQuart",
        });
    }

    return (
        <div id='homepage-body'>
            <div id='homepage-header-container-left' style={{ 
                'width' : SCREEN_WIDTH,
                'height' : SCREEN_WIDTH,
                'top' : `-${SCREEN_HEIGHT * 14 / 10}px`,
                'left' : `-${SCREEN_WIDTH * 3 / 5}px` 
            }} />

            <div id='homepage-header-container-right' style={{ 'color' : COLORS.white, 'height' : SCREEN_HEIGHT }}>
                <div class='navigation-item-container' onClick={scrollToMainInfo}>
                    <Textfit mode="single" forceSingleModeWidth={false}>
                        <p class='navigation-item-text'>Home</p>
                    </Textfit>
                </div>
                <div class='navigation-item-container' onClick={scrollToTimeline}>
                    <Textfit mode="single" forceSingleModeWidth={false}>
                        <p class='navigation-item-text'>Professional<br />History</p>
                    </Textfit>
                </div>
                <div class='navigation-item-container' onClick={scrollToProjects}>
                    <Textfit mode="single" forceSingleModeWidth={false}>
                        <p class='navigation-item-text'>Projects</p>
                    </Textfit>
                </div>
                <div class='navigation-item-container' onClick={scrollToAboutMe}>
                    <p class='navigation-item-text'>About Me</p>
                </div>
                <div class='navigation-item-container' onClick={scrollToContact}>
                    <p class='navigation-item-text'>Contact</p>
                </div>
            </div>

            <div id='homepage-main-info-container' class='homepage-section' style={{ 'height' : SCREEN_HEIGHT }}>
                <div id='homepage-main-info-inner-container'>
                    <div id='homepage-main-info-title' style={{ 'width' : getWidthOfFactContainer(), 'top' : SCREEN_HEIGHT / 8, 'right' : SCREEN_WIDTH / 4}}>
                        <PageTitle title={'TJ.'} />
                    </div>
                    
                    <div id='homepage-main-info-body'>
                        <FactConatiner
                            subtitle={'My name is'}
                            title={'TRISTAN JONG'}
                            text={'I am a Software Engineer that has had many years of experience with development of Web Apps and Phone Apps ranging from personal projects to work experiences. I actively look for any opportunity I can get to further develop my personal skills and knowledge'}
                        />
                    </div>
                </div>

                <div id='homepage-main-info-scroll-arrow'>
                    <ScrollArrow onClick={scrollToTimeline} />
                </div>
            </div>

            <div id='homepage-work-experience-timeline-container' class='homepage-section'>
                <div id='homepage-section-title' style={{ 'color' : COLORS.blueHighlight }}>Professional History</div>
                <Timeline lineColor={'#ddd'}>
                    <CustomTimelineItem 
                        key={'001'}
                        dateText={'Aug 2021 – Present'}
                        titleText={"Associate Software Engineer, Domino's Pizza Enterprises Limited"}
                        subTitleText={'Back-end Developer'}
                        bodyText={
                            <ul>
                                <li>Backend</li>
                                <li>Backend</li>
                                <li>Backend</li>
                                <li>Backend</li>
                                <li>Backend</li>
                            </ul>
                        }
                    />
                    <CustomTimelineItem 
                        key={'002'}
                        dateText={'Sep 2020 – Aug 2021'}
                        titleText={'Software Engineer, MYP Corporation'}
                        subTitleText={'Full-stack Developer'}
                        bodyText={
                            <ul>
                                <li>Managing Tickets and working on ASP.NET MVC, ReactJS, and Transact-SQL code faults and exceptions</li>
                                <li>Developing new features for the different modules</li>
                                <li>Management of workflow and tasks trough Azure</li>
                                <li>Using DevOps to track tasks</li>
                                <li>Client contact through tickets</li>
                            </ul>
                        }
                    />
                    <CustomTimelineItem 
                        key={'003'}
                        dateText={'Jul 2019 – Jan 2020'}
                        titleText={'Software Developer, Biarri'}
                        subTitleText={'Front-end Developer'}
                        bodyText={
                            <ul>
                                <li>Coding in React Native to suit requirements of clients</li>
                                <li>Keep in contact with clients for current information</li>
                                <li>Attend and pitch in daily stand-up meetings to ensure efficient team progression</li>
                                <li>Work on given side programming tasks</li>
                            </ul>
                        }
                    />
                    <CustomTimelineItem 
                        key={'004'}
                        dateText={'Jan 2017 - Current'}
                        titleText={"Instructor, Tan's Taekwondo"}
                        subTitleText={'Poomsae Specialist'}
                        bodyText={
                            <ul>
                                <li>Train, mentor and coach students ages 4 – 20</li>
                                <li>Tidy and lock up before closing of Taekwondo Dojang</li>
                                <li>Open and Prepping of Taekwondo Dojang</li>
                                <li>Address enquiries from parents about their children’s progress</li>
                            </ul>
                        }
                    />
                    <CustomTimelineItem 
                        key={'005'}
                        dateText={'Mar 2016 - Nov 2019'}
                        titleText={"Pizza Maker, Domino's Pizza"}
                        subTitleText={'Instore Worker'}
                        bodyText={
                            <ul>
                                <li>Attend to customer service calls</li>
                                <li>Manage Makeline for production of pizza</li>
                                <li>Clean up work station before closing</li>
                                <li>Ensure all topping and bases are prepped for the following work day</li>
                            </ul>
                        }
                    />
                    <CustomTimelineItem 
                        key={'006'}
                        dateText={'Sep 2015 - Oct 2015'}
                        titleText={"Work Experience Trainee, Footprint IT"}
                        subTitleText={'Assistant'}
                        bodyText={
                            <ul>
                                <li>Answer customer service calls</li>
                                <li>Schedule appointments for PC support</li>
                                <li>Assist in building computers</li>
                                <li>Fix issues with computer towers</li>
                                <li>Troubleshoot software in Windows</li>
                                <li>Troubleshoot hardware of devices</li>
                            </ul>
                        }
                    />
                </Timeline>
            </div>

            <div id='homepage-projects-container' class='homepage-section'>

            </div>

            <div id='homepage-about-me-container' class='homepage-section'>
                
            </div>

            <div id='homepage-contact-container' class='homepage-section'>
                
            </div>
        </div>
    );
}

export default HomePage;