import { COLORS } from '../constants/colors';
import { getWidthOfFactContainer } from '../constants/sizes';
import './FactContainer.style.css';


export const FactConatiner = ({ subtitle, title, text }) => {

    return (
        <div id='fact-container' style={{ 'color' : COLORS.white, 'width' : getWidthOfFactContainer() }}>
            <div id='fact-subtitle'>{subtitle}</div>
            <div id='fact-title'>{title}</div>
            <div id='fact-divider' style={{ 'backgroundColor' : COLORS.blueHighlight, 'width' : '100%' }} />
            <div id='fact-text'>{text}</div>
        </div>
    );
}